import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import ReactJson from 'react-json-view';
import {
  Form,
  Tabs,
  Table,
  Button,
  Modal,
  Row,
  Col,
  message,
  Upload,
  Tooltip,
  Select,
  Icon,
  Spin,
  Typography,
  Progress,
} from 'antd';

import { injectIntl } from 'react-intl';
import {
  getProjects,
  getProcessTypes,
  addJob,
  getJobs,
  saveJob,
  deleteJob,
  getClients,
  getTasks,
} from '../../services';

const { TabPane } = Tabs;
const { Title, Text } = Typography;

const TasksCmp = props => {
  const {
    user,
    match: { params },
    form,
    intl: { messages },
  } = props;

  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(false);
  const [ploading, setPloading] = useState(false);
  const [tasks, setTasks] = useState([]);
  const [jobs, setJobs] = useState([]);
  const [job, setJob] = useState(null);

  const [projects, setProjects] = useState([]);
  const [project, setProject] = useState(null);
  const [processTypes, setProcessTypes] = useState([]);
  const [mode, setMode] = useState('add');

  const [fileList, setFileList] = useState([]);
  const [showError, setShowError] = useState(false);
  const [clients, setClients] = useState([]);

  useEffect(() => {
    fetch();
    fetchTasks();
    fetchProjects();
    fetchProcessType();
    if (user && user.rol === 'admin') {
      fetchClients();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetch = async () => {
    try {
      const { data } = await getJobs();
      setJobs(data.jobs);
    } catch (error) {
      message.error(error.message);
    }
  };
  const fetchTasks = async () => {
    try {
      setLoading(true);
      const { data } = await getTasks();
      if (params.jobId) {
        const aux = data.tasks.filter(item => {
          return item.JobId === parseInt(params.jobId);
        });
        setTasks(aux);
      } else {
        setTasks(data.tasks);
      }
      setLoading(false);
    } catch (error) {
      setLoading(false);
      message.error(error.message);
    }
  };

  const getJobValue = () => {
    const aux = jobs.filter(item => item.id === parseInt(params.jobId));
    if (aux && aux.length) {
      return aux[0];
    }
    return null;
  };

  const fetchProjects = async () => {
    try {
      setPloading(true);
      const { data } = await getProjects();
      setProjects(data.projects);
      setPloading(false);
    } catch (error) {
      setPloading(false);
      message.error(error.message);
    }
  };

  const fetchProcessType = async () => {
    try {
      const { data } = await getProcessTypes();
      setProcessTypes(data);
    } catch (error) {
      message.error(error.message);
    }
  };

  const fetchClients = async () => {
    try {
      const { data } = await getClients();
      setClients(data.clients);
    } catch (error) {
      message.error(error.message);
    }
  };

  const showKPIs = value => {
    Modal.info({
      title: 'KPIs',
      content: (
        <div>
          <ReactJson displayDataTypes={false} name={false} src={JSON.parse(value)} />
        </div>
      ),
      onOk() {},
    });
  };

  const columns = [
    {
      title: 'Id',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Job',
      key: 'JobId',
      render: record => {
        const aux = jobs.filter(item => item.id === record.JobId);
        if (aux && aux.length) {
          return <span>{aux[0].name}</span>;
        }
        return null;
      },
    },
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      title: 'Filename',
      dataIndex: 'filename',
      key: 'filename',
    },
    {
      title: 'Tu To',
      dataIndex: 'tuTo',
      key: 'tuTo',
    },
    {
      title: 'Tu From',
      dataIndex: 'tuFrom',
      key: 'tuFrom',
    },
    {
      title: 'Status',
      dataIndex: 'status',
      key: 'status',
      render: value => {
        if (value === '0') return 'Inactive';
        if (value === '1') return 'Active';
        if (value === '2') return 'Pause';
        if (value === '3') return 'Finished';
      },
    },
    {
      title: 'Wf Status',
      dataIndex: 'wfStatus',
      key: 'wfStatus',
      render: value => {
        if (value === '0') return 'None';
        if (value === '1') return 'Assigned';
        if (value === '2') return 'Accepted';
        if (value === '3') return 'Translation';
        if (value === '4') return 'Revision';
        if (value === '5') return 'Rework';
        if (value === '6') return 'Finished';
      },
    },
    {
      title: 'Progress',
      dataIndex: 'progressPct',
      key: 'progress',
      render: value => {
        return <Progress percent={value || 0} size="small" />;
      },
    },
    {
      title: '',
      key: 'actions',
      width: 150,
      render: (value, record) => {
        return (
          <React.Fragment>
            <Tooltip placement="top" title="KPIs">
              <Button
                style={{ marginRight: 5 }}
                onClick={() => {
                  showKPIs(record.KPIs);
                }}
                size="small"
              >
                KPIs
              </Button>
            </Tooltip>
          </React.Fragment>
        );
      },
    },
  ];

  return (
    <Spin spinning={loading || ploading}>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        {params.jobId && (
          <React.Fragment>
            <Title level={4}>
              <Text underline>Job:</Text> {getJobValue() ? getJobValue().name : null}
            </Title>
          </React.Fragment>
        )}
        <Tabs defaultActiveKey="all">
          <TabPane tab="Tasks" key="all">
            <Table
              size="small"
              loading={loading}
              columns={columns}
              dataSource={tasks}
              rowKey={record => record.id}
            />
          </TabPane>
        </Tabs>
      </div>
    </Spin>
  );
};
const WrappedTasksCmp = Form.create()(injectIntl(TasksCmp));

export default withRouter(WrappedTasksCmp);
